import React from 'react';
import {cleanup, render, screen} from '@testing-library/react-native';

import userEvent from '@testing-library/user-event';
import * as eva from '@eva-design/eva';
import {ApplicationProvider, Layout, Spinner} from '@ui-kitten/components';
import configureStore from '../../redux/store';
import Login from '../Login';
import {loginRequest} from '../../actions';
import {Provider} from 'react-redux';

let store = configureStore();

const email = 'test@test.com';
const password = 'Azerty123@@';

const navigation = {
  navigate: jest.fn(),
};
const setup = () =>
  render(
    <ApplicationProvider {...eva} theme={eva.light}>
      <Provider store={store}>
        <Login navigation={navigation} />
      </Provider>
    </ApplicationProvider>,
  );

describe('Test Login Component', () => {
  beforeEach(() => {
    store.dispatch = jest.fn();
  });

  afterEach(cleanup);

  it('should render all form element', () => {
    setup();

    expect(screen.getByTestId('logo')).toBeDefined();
    expect(screen.getByTestId('email')).toBeDefined();
    expect(screen.getByTestId('password')).toBeDefined();
    expect(screen.getByTestId('login-btn')).toBeDefined();
  });

  it('should show password value after clicking show passwrd icon', () => {
    setup();
    const passwordInput = screen.getByTestId('password');
    expect(passwordInput.props.secureTextEntry).toBe(true);
  });
});
